import axiosClient from "../config/axiosConfig";

export const userlogin = (payload) => {
  return axiosClient("sign", payload, "POST");
};

export const signup = (payload) => {
  return axiosClient("signup", payload, "POST");
};

export const changePassword = (payload, token) => {
  return axiosClient("change-password", payload, "POST", {
    Authorization: token,
  });
};

export const updateProfile = (payload, token) => {
  return axiosClient("update-profile", payload, "POST", {
    Authorization: token,
  });
};

export const getUserDataById = (payload, token) => {
  return axiosClient("get-user-by-id", payload, "POST", {
    Authorization: token,
  });
};

